import { useRouter } from "./hooks/useRouter";
import { AppPage } from "./partials/app-page";
import { Home } from "./pages/home";
import { About } from "./pages/about";
import "./styles/variables.css";
import "./styles/global.css";
var router = useRouter();
var app = document.createElement("div");
app.classList.add("app");
document.body.appendChild(app);
var state = {
    currentRoute: undefined,
    defaultRoute: "home"
};
var handleChange = function (route) {
    var defaultRoute = state.defaultRoute, currentRoute = state.currentRoute;
    var routeName = route.split("/")[1] || defaultRoute;
    if (currentRoute === routeName)
        return;
    app.replaceChildren();
    switch (routeName) {
        case "about":
            app.appendChild(AppPage({ router: router, defaultRoute: defaultRoute, content: About() }));
            break;
        default:
            app.appendChild(AppPage({ router: router, defaultRoute: defaultRoute, content: Home() }));
    }
    state.currentRoute = routeName;
};
router.initRouter(handleChange);
